.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-weight: bold;
  font-stretch: expanded;
  color: white;
}

.App-link {
  color: #61dafb;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
li {
  transition: all 0.3s ease;
}

li.completed {
  color: #999;
}

input[type="text"], button {
  transition: all 0.3s ease;
}

input[type="text"]:focus {
  border-color: #61dafb;
}

button:hover {
  background-color: #61dafb;
  color: #fff;
}

.manage {
  display: flex;
  flex-direction: space-evenly;
  align-items: center;
  margin: 8px;
  padding: 5px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.manage h2 {
  font-size: 16px;
  text-align: center;
  margin: 0;
  padding: 0;
}

.manage form {
  margin-left: 35px;
  padding: 8px;
  width: 70%;
  border-radius: 8px;
}

.manage button {
  background-color: #61a6fb;
  border: none;
  width: 70px;
  font-size: 11px;
  padding: 5px;
  margin-left: 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}